import React, { Component, useEffect, useState } from 'react'
import { connect } from 'react-redux'
import FormControl from '../../../../components/pages-component/form-controls/components/form-control'
import '../../../../components/pages-component/button/button.css'
import * as actionType from '../../../../store/actions/action-type'
import * as utilityFunctions from '../../../../utility-functions/utility-functions'
import postMedia from '../../../../services/api/post-media'
import mediaUpdate from '../../../../services/api/media-update'
import ErrorBox from '../../../../components/pages-component/form/components/error-box';
import Loader from '../../../../components/ui/loader/loader';
import uuid from 'uuid'

const MediaForm = (props) => {

    const [title, settitle] = useState("");
    const [link, setlink] = useState("");
    const [publisher, setpublisher] = useState("");
    const [thumbnail, setthumbnail] = useState("");
    const [description, setdescription] = useState("");
    const [published_at, setpublished_at] = useState("");
    const [mediaId, setmediaId] = useState("");
    const [id, setid] = useState(uuid());
    const [publish, setpublish] = useState(false);
    const [formValid, setformValid] = useState(true);
    const [errorMsg, seterrorMsg] = useState(null);
    const { formSubmissionStart, setformSubmissionStart } = useState(false);

    useEffect(() => {
        if (props.mediaId) {
            const filterMedia = props.mediaItems.filter((data) => data.id === props.mediaId);
            filterMedia.map((data) => {
                settitle(data.title)
                setlink(data.link)
                setpublisher(data.publisher)
                setthumbnail(data.thumbnail)
                setdescription(data.description)
                setpublished_at(data.published_at)
                setpublish(data.publish)
                setid(data.id)
            })
        }
    },[])

    const submitCallBack = (data) => {
        let code = 201, mediaData = onAddMedia()
        if (props.updateMedia) {
            code = 200
        }
        if (data.status === code) {
            clearForm()
            props.closeModal()
            utilityFunctions.clearSelectField('.select-input');
            if (props.updateMedia) {
                props.updateMedias(mediaData)
            } else {
                props.postMedia(mediaData)
            }
            setformValid(true)
            seterrorMsg(null)
            setformSubmissionStart(false)
        } else if (data.response) {
            setformValid(false)
            seterrorMsg(data.response.data)
            setformSubmissionStart(false)
        } else {
            const genericErrorMsg = { Error: ["Oops! Something went wrong, please try again."] };
            setformValid(false)
            seterrorMsg(genericErrorMsg)
            setformSubmissionStart(false)
        }
    }

    const onMediaSubmit = (e) => {
        e.preventDefault();
        setformSubmissionStart(true)
        const data = onAddMedia()
        if (props.updateMedia) {
            mediaUpdate(submitCallBack, id, data)
        } else {
            if (data) {
                postMedia(submitCallBack, data)
            }
        }
    };

    const handleInputChange = (e) => {
        e.target.name(e.target.value)
        const target = e.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        name(value);
    }

    const onAddMedia = () => {
        const mediaData = {
            title: title,
            link: link,
            publisher: publisher,
            thumbnail: thumbnail,
            description: description,
            published_at: published_at,
            publish: publish,
            id: id
        };
        return mediaData
    };

    const clearForm = () => {
        settitle('')
        setlink('')
        setpublisher('')
        setthumbnail('')
        setdescription('')
        setpublished_at('')
        setpublish(false)
    }

    return (
        <form onSubmit={onMediaSubmit}>
            <FormControl>
                <label htmlFor="title">
                    title
                        <span className="star">*</span>
                </label>
                <input
                    type="text"
                    name="title"
                    placeholder="Enter the title"
                    className="input-element"
                    id="title"
                    value={title}
                    onChange={e => handleInputChange(e)}
                    required={true}
                />
            </FormControl>
            <FormControl>
                <label htmlFor="link">
                    link
                        <span className="star">*</span>
                </label>
                <input
                    type="text"
                    name="link"
                    placeholder="Enter the media link"
                    className="input-element"
                    id="link"
                    value={link}
                    onChange={e => handleInputChange(e)}
                    required={true}
                />
            </FormControl>
            <FormControl>
                <label htmlFor="publisher">
                    publisher
                        <span className="star">*</span>
                </label>
                <input
                    type="text"
                    name="publisher"
                    placeholder="Enter the publisher name"
                    className="input-element"
                    id="publisher"
                    value={publisher}
                    onChange={e => handleInputChange(e)}
                    required={true}
                />
            </FormControl>
            <FormControl>
                <label htmlFor="published_at">
                    published_at
                        <span className="star">*</span>
                </label>
                <input
                    type="date"
                    name="published_at"
                    placeholder="Enter the publisher name"
                    className="input-element"
                    id="publisher"
                    value={published_at}
                    onChange={e => handleInputChange(e)}
                    required={true}
                />
            </FormControl>
            <FormControl>
                <label htmlFor="thumbnail">
                    image
                    </label>
                <input
                    type="text"
                    name="thumbnail"
                    placeholder="Enter the image link"
                    className="input-element"
                    id="thumbnail"
                    value={thumbnail}
                    onChange={e => handleInputChange(e)}
                />
            </FormControl>
            <FormControl>
                <label htmlFor="description">
                    description
                    </label>
                <textarea
                    type="textarea"
                    name="description"
                    placeholder="Enter the description"
                    className="input-element"
                    id="description"
                    value={description}
                    onChange={e => handleInputChange(e)}
                />
            </FormControl>
            <div style={{ margin: 5 }}>
                <input
                    type="checkbox"
                    name="publish"
                    id="publish"
                    onChange={e => handleInputChange(e)}
                    checked={publish}
                />{" "}<span><b>Publish</b></span>
            </div>
            { errorMsg ? <ErrorBox isRequired errorMsg={errorMsg} /> : null}
            { formSubmissionStart ? <Loader>Submitting your request...</Loader> : null}
            <button type="submit" className="form-btn" onClick={onAddMedia}>Submit</button>
        </form>
    )

}

const mapStateToProps = (state) => {
    return {
        mediaItems: state.media.mediaItems
    };
};

const mapDispathcToProps = (dispatch) => {
    return {
        postMedia: (data) => {
            dispatch({
                type: actionType.POST_MEDIA_ITEMS,
                value: data
            })
        },
        updateMedias: (data) => {
            dispatch({
                type: actionType.UPDATE_MEDIA,
                value: data
            });
        },
    }
}

export default connect(mapStateToProps, mapDispathcToProps)(MediaForm)
