import React from 'react';
import axios from '../services/axios/axios';
import headers from '../services/axios/headers';

import * as actionType from '../constants/actionTypes';

  // GET MEDIA
export const getMedia3 = async (callback) => {
    axios.get('/medias/', { headers })
        .then(res => {
            // console.log(res.data);
            callback(res.data);
            console.log('in the action state!!')
        })
        .catch(err => {
            callback({
                status: 'error',
                data: err
            });
        });
};

export const onUpdateMediaItems3 =  (data) => async (dispatch) => {
    console.log(data)
    dispatch({
        type: actionType.UPDATE_MEDIA_ITEMS,
        value: data
    })
}

export const onUpdateFirstRun3 =  (data) => async(dispatch)=> {
    console.log(data)

    dispatch({
        type: actionType.UPDATE_FIRST_RUN_MEDIA,
        value: data
    })
}