import React, {  useEffect, useState,useContext } from 'react';
import { connect } from 'react-redux';
import LazyLoad from 'react-lazyload';
import getMedia from '../../services/api/get-media'
import * as actionType from '../../store/actions/action-type';
import Modal from '../../components/ui/modal/modal'
// importing the API used for this page
import pubUnpubMedia from '../../services/api/media-unpublised' 
// importing UI component used
import PageBanner from '../../components/ui/page-banner/page-banner';
import MediaItem from './components/media-item/media-item';
import ContentLoader from '../../components/ui/content-loader/content-loader';
import './media.css';
import '../../components/pages-component/button/button.css'
// import { Helmet } from "react-helmet";
import DocumentMeta from 'react-document-meta';
import { useDispatch, useSelector } from "react-redux";
import {getMedia3, onUpdateMediaItems3,onUpdateFirstRun3} from '../../actions/mediaActions'
import ShowMsg from '../../components/pages-component/showMSG/msg'
import MediaForm from './components/media-form/media-form';
import Header from '../../components/layout/header/header';
import Footer from '../../components/layout/footer/footer';
import MediaContext from '../../context2/mediaContext/mediaContext'
const Media = (props) => {


  const mediaContext = useContext(MediaContext)
  const dispatch  = useDispatch()
  const {mediaItems, firstRun} = useSelector(state => state.media)
  const {session} = useSelector(state=> state.login)
  // const {getMedia2, onUpdateMediaItems2,onUpdateFirstRun2,mediaItems} = mediaContext;
  const [ loadingContent, setloadingContent ] = useState(firstRun);
  const [ showModal, setshowModal ] = useState(false);
  const [ createMedia, setcreateMedia ] = useState(false);
  const [ updateMedia, setupdateMedia ] = useState(false);
  const [ mediaId, setmediaId ] = useState('');
  const [ msg, setmsg ] = useState(false);
  const [ msgContent, setmsgContent ] = useState('');
  const [ attention, setattention ] = useState(false);


  // const m = useSelector(state=> state.media2)
  // const {  mediaItems, designfilesItems } = m
// const {mediaItems} = useSelector(state.media)
  const onGetMedia = (data) => {
    setloadingContent(false);
    if (data.status === 'error') {
      data.data.response ? console.log(data.data.response) : console.log(data.data);
    } else {
      // props.onUpdateMediaItems(data);
      // props.onUpdateFirstRun(false);
      // onUpdateMediaItems2(data);
      // onUpdateFirstRun2(false);
      dispatch(onUpdateMediaItems3(data));
      dispatch(onUpdateFirstRun3(false));
    }
  }

  useEffect(() => {
    document.title = `MIA | Ethereal Machines | Bangalore, India`;
    // scrolling the component to the top when the page is mounted
    window.scrollTo(0, 0);
    console.log(mediaItems)
    /**
     * If the component is being loaded for the first time
     * then fetch the media items available on the server. If component is
     * not loaded for the first time, no need to fetch the media
     * items from the server. The Redux Store will have all the
     * available items.
    */
    if (firstRun) {
      // getMedia2(onGetMedia);
      getMedia3(onGetMedia)
      // getMedia2(onGetMedia);
      // getMedia(onGetMedia);
  
    }
  },[])

  const addMedia = () => {
    setshowModal(true)
    setcreateMedia(true)
  };
  const closeModal = () => {
    setshowModal(false)
    setmediaId('')
    setmsg(false)
    setmsgContent('')
    setattention(false)
    setcreateMedia(false)
    setupdateMedia(false)
  }
  const onUpdate = (id) => {
    setshowModal(true)
    setmediaId(id)
    setupdateMedia(true)
  };

  const unPublishCallback = (data) => {
    if (data.status === 200) {
      setmsg(true)
      setmsgContent('Successfully Changed')
      setshowModal(true)
      setattention(false)
    } else {
      setmsg(true)
      setmsgContent('Something Went Wrong')
      setshowModal(true)
      setattention(false)
    }
  };

  const publishMedia = (id) => {
    const data = {
      id: id,
      publish: true
    }
    pubUnpubMedia(unPublishCallback, id, { publish: true })
    props.pubunpubMedia(data)
  };
  const unpublishMedia = (id) => {
    const data = {
      id: id,
      publish: false
    }
    pubUnpubMedia(unPublishCallback, id, { publish: false })
    props.pubunpubMedia(data)
  };

  const deleteJobCallback = (data) => {
    if (data.status === 204) {
      setmsg(true)
      setmsgContent('Successfully Deleted')
      setshowModal(true)
      setattention(false)
    } else {
      setmsg(true)
      setmsgContent('Something Went Wrong')
      setshowModal(true)
      setattention(false)
    }
  };
  const deleteMedia = (id) => {
    deleteMedia(deleteJobCallback, id)
    props.deleteMedia(id)
  };

  const media = mediaItems.map(item => {
    return (
      <LazyLoad height={200} offset={100} placeholder={<ContentLoader />} once key={item.id}>
        <MediaItem
          item={item}
          onUpdate={onUpdate}
          session={session.session}
          publishMedia={publishMedia}
          unpublishMedia={unpublishMedia}
          deleteMedia={deleteMedia}
        />
      </LazyLoad>
    );
  });

  let mediaLoader = [];

  for (let i = 0; i < 6; i++) {
    mediaLoader.push(
      <ContentLoader key={i + '_loader'} />
    );
  }
  const meta = {
    title: 'Media Coverage – CNC Machine,3D printer |Ethereal Machines-Bangalore, India.',
    meta: {
      property: {
        'title': 'Media Coverage – CNC Machine,3D printer |Ethereal Machines-Bangalore, India.',
        'description': 'Ethereal Machines – Media Coverage: simultaneous 5-axis CNC Machine & 3D Printer.',
      }
    }
  };
  return (
    <React.Fragment>
      <div className="page page--media">
        <Header />
        {/* <Helmet>
        <title>Media Coverage – CNC Machine,3D printer |Ethereal Machines-Bangalore, India.</title>
        <meta name="description" content="Ethereal Machines – Media Coverage: simultaneous 5-axis CNC Machine & 3D Printer."/>
        </Helmet> */}
        <DocumentMeta {...meta} />

        {
          msg
            ?
            <Modal show={showModal} clicked={closeModal}>
              <ShowMsg msg={msgContent} attention={attention} />
            </Modal>
            : null
        }
        {
          updateMedia || createMedia
            ?
            <Modal show={showModal} clicked={closeModal} >
              <MediaForm
                mediaId={mediaId}
                closeModal={closeModal}
                updateMedia={updateMedia}
              />
            </Modal>
            : null
        }
        <PageBanner heading={"Ethereal Machines in Media"} classValue={'page-banner--media'} />
        <section className="section section--media">
          <div className="container">
            {/* <SectionHeading name={"What media says about us"} classValue={"u-margin-bottom-big u-text-center"}/> */}
            {
              session.session
                ?
                <button className="form-btn" onClick={addMedia}>Add new media</button>
                :
                null
            }
            <div className="media-container">
              {
                loadingContent ?
                  mediaLoader :
                  media
              }
            </div>
          </div>
        </section>
        {/*<section className="section section--contact-us">
            <div className="container">
                <SectionHeading name={"Contact Us"} classValue={"u-margin-bottom-big u-text-center"}/>
                <ContactUsBanner
                  firstText={"We are excited to tell you more about our story."}
                  secondText={"Let's get in touch."}
                />
            </div>
              </section> */}
        <Footer />
      </div>
    </React.Fragment>
  );

}

// const mapStateToProps = (state) => {
//   return {
//     firstRun: state.media.firstRun,
//     mediaItems: state.media.mediaItems,
//     session: state.login
//   };
// };

const mapDispatchToProps = (dispatch) => {
  return {
    // onUpdateFirstRun: (firstRun) => {
    //   dispatch({
    //     type: actionType.UPDATE_FIRST_RUN_MEDIA,
    //     value: firstRun
    //   });
    // },
    // onUpdateMediaItems: (mediaItems) => {
    //   dispatch({
    //     type: actionType.UPDATE_MEDIA_ITEMS,
    //     value: mediaItems
    //   });
    // },
    pubunpubMedia: (data) => {
      dispatch({
        type: actionType.UNPUBLISHED_MEDIA,
        value: data
      });
    },
    deleteMedia: (data) => {
      dispatch({
        type: actionType.DELETE_MEDIA,
        value: data
      });
    },
  };
};

export default connect(mapDispatchToProps)(Media);
